import { Component, OnInit } from '@angular/core';
import { ChatService } from '../../../services';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-chat3-dashboard',
  templateUrl: './chat3-dashboard.component.html',
  styleUrls: ['./chat3-dashboard.component.scss']
})

export class Chat3DashboardComponent implements OnInit {
  messages: Observable<any>;
  user: { photo: string };
  constructor(private service: ChatService) {}

  ngOnInit() {
    this.showMessage();
  }

  showMessage() {
    this.messages = this.service.routerChat('chat3');
    this.service.user(2).subscribe((data: string[]) => this.user = { photo: data[3]});
  }

}
